import React, { Component } from 'react';
import './viewrating.css';
import LoadingOverlay from 'react-loading-overlay';
import axios from 'axios';
import {Breadcrumb, BreadcrumbItem} from 'reactstrap';
class Viewrating extends Component {

  constructor(props) {
    super(props);
    this.state = {
      viewrate : [],
      user:[],
      isActive:true,
      Viewrating:[]
      // id: "5d8a1413debc645050082c61",
    }
  }

  componentDidMount() {

      axios
      .get('https://cors-anywhere.herokuapp.com/http://13.233.64.181:5000/api/getchild/'+ this.props.location.state.className+ "/"+ this.props.location.state.usrid)
      .then(res => this.setState({ user: res.data.data }))
      .catch(err => console.log(err))

      axios
      .get('https://cors-anywhere.herokuapp.com/http://13.233.64.181:5000/api/getweekly1/' + this.props.location.state.className + "/"+ this.props.location.state.usrid)
      .then(res => this.setState({ viewrate: res.data.data, Viewrating: res.data}))
      .catch(err => console.log(err))
      .then((res) => {
        console.log("Result"+ JSON.stringify(res))
        if(JSON.stringify(this.state.Viewrating.success) === "true"){
    this.setState({
        isActive: false
    });
}
})
  }

  render() {
    console.log("view rate" +JSON.stringify(this.state.viewrate));
    console.log("users" + JSON.stringify(this.state.user));
    return( 
      <div>
         <LoadingOverlay
  active = {this.state.isActive}
  spinner
  text='Loading your content...'
  >
      <br/>
      <Breadcrumb>
        <BreadcrumbItem>Home </BreadcrumbItem>
        <BreadcrumbItem>Student list </BreadcrumbItem>
        <BreadcrumbItem active style={{ color: "#ffa901" }}>View Rating</BreadcrumbItem>
      </Breadcrumb>
        <div class = "col-sm-12">
        <img src = {this.state.user.Image} alt = "male img" height = "200px" className = "center"/>
        <h1 style = {{textAlign: "center"}}> {this.state.user.first_name}&nbsp;{this.state.user.middle_name}&nbsp;{this.state.user.last_name}</h1>
        </div>
        <div class = "card" style = {{backgroundColor:"white"}}>
        <table style={{ marginTop: "0px" }}>
            {this.state.viewrate.map((rate) => {
       var topics = rate.topics.map((top) => {
         var questions = top.questions.map((ques) => {
           
            
            return <table>
             <tr>
            <th style = {{backgroundColor:"lightgray"}}>&nbsp; &nbsp;Question</th>
            <th style = {{textAlign:"center", backgroundColor:"lightgray"}}>&nbsp; &nbsp;<span style = {{color:"lightgray"}}>....</span>Rating<span style = {{color:"lightgray"}}>....</span></th>
          </tr>
            <tr>
            <td style={{ textAlign: "center", backgroundColor:"white" }} >{ques.question}</td> 
            <td style={{ textAlign: "center", backgroundColor:"white" }} >{ques.rating}&nbsp;&nbsp;</td>
            </tr>
            </table>
            

           
         })
         return(
           
             <tr >
         {/* <th style = {{backgroundColor:"lightgray"}}>Topic</th> */}
       <td style={{textAlign: "center", backgroundColor:"white"}}><h3>{top.topic}</h3></td>
       {questions}
    </tr>
           
         )
       })
    return(
      <table style={{ marginTop: "0px" }} >
      {topics} 
      </table>

    )
    })}
       </table>
 

    </div>
    </LoadingOverlay>

    </div>
    )
  }
}

export default Viewrating;
